import React, { useState } from "react";
import { Link } from "react-router-dom";
import './Navbar.css'
import { Container, MenuLabel, Icon, NavBackground, Navigation, List, ItemLink, Wrapper, LogoHeading, LogoTag } from './Navbar.style';


function Navbar(props) {
    const [click, setClick] = useState(false);
    const handleClick = () => setClick(!click);
    return (
        <>

            {/* Desktop View Code Start Here */}

            <div className="desktopContainer">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-lg-3">
                            <div className="logoContainer">
                                <Link to="/">
                                    <h1 className="logoHeading">ALLSTARSPACE</h1>
                                    <p className="logoTag">The Sky Is The Limit</p>
                                </Link>
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <nav className="header_menu">
                                <ul>
                                    <li className={props.name}>
                                        <Link to="/About">About us</Link>
                                    </li>
                                    <li className={props.name1}>
                                        <Link to="/News">News</Link>
                                    </li>
                                    <li className={props.name2}>
                                        <Link to="/Shop">Merchandise</Link>
                                    </li>
                                    <li className={props.name3}>
                                        <Link to="/Contact">Contact</Link>
                                    </li>
                                    <li className={props.name4}>
                                        <Link to="/admin">Admin</Link>
                                    </li>
                                </ul>
                            </nav>
                        </div>
                    </div>
                </div>
            </div>

            {/* Desktop View Code Start Here */}

            {/* Mobile Header Code Start Here */}

            <Container>
                <Wrapper>
                    <Link to="/">
                        <LogoHeading>ALLSTARSPACE</LogoHeading>
                        <LogoTag>The Sky Is The Limit</LogoTag>
                    </Link>
                </Wrapper>
                <MenuLabel htmlFor="navi-toggle" onClick={handleClick}>
                    <Icon clicked={click}>&nbsp;</Icon>
                </MenuLabel>
                <NavBackground clicked={click}>&nbsp;</NavBackground>

                <Navigation clicked={click}>
                    <List>
                        <li>
                            <ItemLink onClick={handleClick} to="/">
                                Home
                            </ItemLink>
                        </li>
                        <li>
                            <ItemLink onClick={handleClick} to="/About">
                                About
                            </ItemLink>
                        </li>
                        <li>
                            <ItemLink onClick={handleClick} to="/News">
                                News
                            </ItemLink>
                        </li>
                        <li>
                            <ItemLink onClick={handleClick} to="/Shop">
                                Shop
                            </ItemLink>
                        </li>
                        <li>
                            <ItemLink onClick={handleClick} to="/Contact">
                                Contact Us
                            </ItemLink>
                        </li>
                    </List>
                </Navigation>
            </Container>

            {/* Mobile View Code End Here */}
        </>
    );
}

export default Navbar;