import React from "react";
import "./Hero.css";
import { HiOutlinePlay } from "react-icons/hi";
import JensImage from "../../assets/img/Jens-img.png";

function Home() {
    return (
        <>
            <section>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-12 col-xxl-6 order-1 order-xxl-0 d-flex justify-content-center justify-content-xl-start">
                            <div className="Wrapper">
                                <div className="Title">
                                    <h3>ALLSTARSPACE</h3>
                                </div>
                                <div className="SubTitle">
                                    <p>Change the way you feel about tech news!</p>
                                </div>
                                <div className="Description">
                                    <p>
                                        No idea is too small, all sorts of ideas have the potential
                                        to change the world as we know it for the better.<br></br>
                                        We are here to change the narrative for tech news!
                                    </p>
                                </div>
                                <a
                                    href="https://www.youtube.com/channel/UCXpJGo5u2rQEVTb-mioq4Pg"
                                    target="_blank">
                                    <button className="button">
                                        <HiOutlinePlay style={{ marginRight: "20px" }} />
                                        WATCH ON YT
                                    </button>
                                </a>
                            </div>
                        </div>
                        <div className="col-12 col-xxl-6 d-flex justify-content-center justify-content-xxl-end order-0 order-xxl-1 jensImgHero">
                        <img src={JensImage} alt="Jens Image" className="img-fluid" />
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Home;
