import React from 'react'
import './AboutJens.css'
import aboutJensImage from '../assets/img/aboutJensImage.jpg'


function AboutDetails() {
    return (
        <>
            <div className="container-fluid aboutJensContainer">
                <div className="row aboutJensWrapper">
                    <div className="col-xl-2">
                        <img src={aboutJensImage} alt="About Jens Image" className="aboutJensImage img-fluid" />
                    </div>
                    <div className="col-xl-10 pl-xl-5">
                        <h1>About me!!</h1><br></br>
                        <p>Every day, I am trying to change the world….for you.<br></br><br></br>
                            Hello!<br></br><br></br>
                            Jens here, founder of Allstarspace.<br></br><br></br>
                            It’s not every day I get to speak with you, so I am glad you are here.<br></br><br></br>
                            There is something I can’t get out of my head that involves you. Three years ago, we were searching for a rescue dog, our idea of giving something back. We found the most loving dog you can imagine. Her name is DJ. The day we picked her up, the woman running the shelter said “You know Jens, we can’t change the world, people will always be cruel, but we can change the world for someone else” meaning DJ.<br></br><br></br>
                            Whilst DJ has become the most influential member in our household, the message followed me around, no matter what.<br></br><br></br>
                            So, I started my YouTube Channel and this Site, because of this remarkable experience.<br></br><br></br>
                            Every day, I am trying to change the world….for you.</p><br></br>
                        <h3>10 Random Facts about me</h3><br></br>
                        <ul>
                            <li>I grew up in Berlin and was amongst the first ones to tear down the Berlin wall in November 1989.</li>
                            <li>Anything but mainstream TV shows.</li>
                            <li>I got married twice, once in Trinidad and once in Las Vegas (not at the same time).</li>
                            <li>If I could pick one superpower, it would be to be invisible. Imagine what you could do.</li>
                            <li>I believe DJ was sent to us for a reason, she behaves like a human being in disguise.</li>
                            <li>Growing up, I wanted to become an actor until I found out how hard it really is…OMG.</li>
                            <li>I frequently find myself reminiscing about the eighties and nineties. Good old times.</li>
                            <li>As you would expect, Schnitzel is my thing.</li>
                            <li>I love cars, my parents never had one, I had to walk for 18 years or had to take the bike. So I compensate for those “lost” 18 years now.</li>
                            <li>Munich is the best town to live in Germany (My friends in Berlin can never know)</li>
                        </ul>
                        <p>What’s next</p><br></br>
                        <p>You tell me, send me an email.</p>
                    </div>
                </div>
            </div>
        </>
    )
}

export default AboutDetails
