import React from "react";
import './AboutDj.css'
import aboutDjImage from "../assets/img/aboutDjImage.jpg";


function AboutDetails() {
    return (
      <>
            <div className="container-fluid aboutDjContainer">
                <div className="row aboutDjWrapper">
                    <div className="col-xl-10 pr-xl-5 order-xl-0 order-1">
                        <h1>About DJ</h1><br></br>
                        <p>
                            DJ is a rescue dog, known as a Pomeranian. Her purpose was to
                            bread little DJ babies before she was handed over to a shelter.
                            She is 6 years old and is probably the most chilled-out dog you
                            will ever meet. In fact, she has what I call a stealth mode, she
                            is suddenly with you, out of nowhere she appears and sits behind
                            you, looking up, expecting love and a belly rub, leaving you
                            wondering…how on earth did she manage to sneak up on me like
                            this?
                            <br></br><br></br>
                            If you look up the word companion in a dictionary, a picture of
                            DJ should come up. She is THE ultimate companion and will never
                            leave your side.
                        </p><br></br>
                        <h3>10 Random Facts about her</h3><br></br>
                            <ul>
                                <li>She loves a belly rub at any time, day or night.</li>
                                <li>Meat is her thing.</li>
                                <li>
                                    She will not grow higher, only sideways, depending on how
                                    much food she had.
                                </li>
                                <li>
                                    Behind a fence or hedge, she comes after you, she feels
                                    untouchable. If there is none, she hides behind me.
                                </li>
                                <li>
                                    She is jealous of anybody and will let you know, by gently
                                    asserting herself into the occasion.
                                </li>
                                <li>
                                    She is the most finicky dog you will ever know. (Especially
                                    food)
                                </li>
                                <li>
                                    When she wants to come to bed at night she presents her
                                    “Blue Steel” look from Zoolander. (It works)
                                </li>
                                <li>Sometimes she looks like a fox.</li>
                                <li>
                                    When she really needs a belly rub, she uncontrollably
                                    launches backward and lands on her back….leaving the rest up
                                    to you.
                                </li>
                            </ul>
                    </div>
                    <div className="col-xl-2 order-xl-1 order-0">
                        <img
                            src={aboutDjImage}
                            alt="About Dj Image"
                            className="aboutDjImage images"
                        />
                    </div>
                </div>
            </div>
      </>
    );
}
 
export default AboutDetails;
