import React from 'react'
import './BlogSection.css'
import { Link } from 'react-router-dom'

function BlogSection(props) {
    const PF = "http://44.202.13.47:8000/images/"
    return (
        <>

            {/* {console.log(props.postDetail[0])} */}
            <section className="blogPagePosts">
                <div className="container-fluid Container">
                    <div className="row">

                        {props.postDetail.map((item, i) => {
                            return (
                                <div className="col-xl-3 col-md-6">
                                    <div className="Card" style={{ width: "100%" }}>
                                        <Link to="/BlogDetails">
                                            {item.photo && (
                                                <img
                                                    src={PF + item.photo}
                                                    class="card-img-top"
                                                    alt="..."
                                                />
                                            )}

                                        </Link>
                                        <div className="Cover" className="card-body news-body">
                                            <p className="Category">Apple</p>
                                            <Link to={`/post/${item._id}`}><h3 className="newTitle">
                                                {item.title}
                                            </h3></Link>
                                            <p className="AuthorNameDate">AllstarSpace - {new Date(item.createdAt).toDateString()}</p>
                                        </div>
                                    </div>
                                </div>
                            ) 
                        })}

                    </div>
                </div>
            </section>
        </>
    )
}

export default BlogSection
