import React from 'react'
import Navbar from '../Navbar/Navbar'
import FixedYt from '../Fixed-yt/FixedYt'
import Hero from '../HomeComponents/Hero/Hero'
import Slider from '../HomeComponents/Slider/YouTubeSlider'
import News from '../HomeComponents/News/News'
import OurProduct from '../HomeComponents/OurProduct/OurProduct'
import Testimonial from '../HomeComponents/Testimonial/Testimonial'
import Footer from '../Footer/Footer'
import pack from "../../../package.json"
import { useState, useEffect } from "react";
import axios from 'axios'

function Home() {
    const [postDetail,setPostDetail]=useState([])

    useEffect(()=>{
        const fetchPostData=async()=>{
            const res1=await axios.get(`${pack.proxy}post`)
            setPostDetail(res1.data)
            console.log(res1.data);
        }
        fetchPostData();
    },[])
    
    return (
        <>
            
            <Navbar />
            <FixedYt />
            <Hero />
            <Slider />
            <News postDetail={postDetail} />
            <OurProduct />
            <Testimonial />
            <Footer />
        </>
    )
}

export default Home
