import React from 'react';
import { BrowserRouter, Route, Routes } from "react-router-dom";
import './App.css';
import BlogDetails from './components/BlogComponents/BlogDetails/BlogDetails';
import { CustomCursor } from './components/Cursor/CustomCursor';
import About from './components/Page/About';
import News from './components/Page/News';
import Contact from './components/Page/Contact/Contact';
import Home from './components/Page/Home'
import Shop from './components/Page/Shop';
import SingleProduct from './components/ShopPageComponents/SingleProdutPage/SingleProduct';
import { useContext } from "react";
import { Context} from "./admin/context/context"
import Homepage from "./admin/pages/homepage/Homepage";
import Login from "./admin/pages/login/Login";

import Settings from "./admin/pages/settings/Settings";
import Single from "./admin/pages/single/Single";
import Write from "./admin/pages/write/Write";
function App() {
  const {user}=useContext(Context)
  return (
    <>
    <CustomCursor />
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/About" element={<About />} />
          <Route path="/News" element={<News />} />
          <Route path="/post/:postId" element={<BlogDetails />} />
          <Route path="/Shop" element={<Shop />} />
          <Route path="/SingleProduct" element={<SingleProduct />} />
          <Route path="/Contact" element={<Contact />} />

          <Route exact path="/admin" element={user ? <Homepage /> : <Login />} />
          {/* <Route exact path="/admin/home" element={<Homepage />} /> */}
          {/* <Route exact path="admin/allstar" element={user ? <Homepage /> : <Login />}/> */}

          <Route  path="admin/write" element={user ? <Write /> : <Login />}/>
        <Route exact path="admin/settings"  element={user ? <Settings /> : <Login/>}/>
        <Route exact path="admin/post/:postId" element={<Single />}/> 
        
        </Routes>
      </BrowserRouter>
    </>
  );
}

export default App;
