import React from "react";
import "./News.css";
import { Link } from 'react-router-dom'


function News(props) {
    const PF = "http://44.202.13.47:8000/images/";
   
    return (
        <>
         {console.log(props.postDetail[0])}
            <div className="container-fluid">
                <div className="row">
                    <div className="newsHeadingContainer">
                        <div className="col-12 d-flex justify-content-between align-items-center">
                            <h1>News</h1>
                            <Link to="/News"><p>View All</p></Link>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container-fluid Container">
                <div className="row">


                    {
                        props.postDetail.slice(0, 4).map((item, i) => {
                            return (
                                <div className="col-xl-3 col-md-6" key={i}>
                                    <div className="Card" style={{ width: "100%" }}>
                                        <a href="/">
                                            {item.photo && (

                                                <img
                                                    src={PF + item.photo}
                                                    class="card-img-top"
                                                    alt="..."
                                                />
                                            )}

                                        </a>
                                        <div className="Cover" className="card-body news-body">
                                            {/* <p className="Category">Apple</p> */}
                                            <Link to={`/post/${item._id}`}>
                                            <h3 className="newTitle">
                                                {item.title}
                                            </h3>
                                            </Link>
                                            
                                            <p className="AuthorNameDate">AllstarSpace - {new Date(item.createdAt).toDateString()}</p>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </>
    );
}

export default News;
