import React,{ Component } from 'react';
import './SliderStyle.css'
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import axios from "axios";
const responsive = {
  superLargeDesktop: {
    // the naming can be any, depends on you.
    breakpoint: { max: 1920, min: 1024 },
    items: 3
  },
  desktop: {
    breakpoint: { max: 1024, min: 768 },
    items: 2
  },
  tablet: {
    breakpoint: { max: 768, min: 635 },
    items: 2
  },
  mobile: {
    breakpoint: { max: 635, min: 0 },
    items: 1
  }
};

 var finalUrl = ` https://www.googleapis.com/youtube/v3/search?key=AIzaSyBMztxH9XuBq1dGflbKO4sDGodJtcRW3mc&channelId=UCXpJGo5u2rQEVTb-mioq4Pg&part=snippet,id&order=date&maxResults=10`;

//




export default class YouTubeSlider extends Component {

  state = {
    resultyt: [],
    loading: false,
};
showVideo = () => {
          axios
             .get(finalUrl)
              .then((res) => {
                  //  console.log(res.data.items);
                  this.setState({ resultyt: res.data.items, loading: true });
              })
              .catch(function (error) {
                  // handle error
                  console.log(error);
              });
      };
      componentDidMount() {
          //this.setState({loader:true})
          this.showVideo();
      }






  render() {
    console.log(this.state.resultyt);
    return <>
    <Carousel responsive={responsive} className='YSliderContainer' style={{padding: "0 79px"}}>

    {
  this.state.resultyt.map((link,i)=>{
    return (
      <div className="yYSlider">
          <div className="youtubeiframe">
            <iframe src={"https://www.youtube.com/embed/" + link.id.videoId}title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
          </div>
          <p className='date'>{link.snippet.publishTime}</p>
          <div className='YtSliderTitle'>
          <p>{link.snippet.title}</p>
          </div>
        </div>

    )
  })
}


       
        
        
        
       
        
        
       
  </Carousel>;
    </>;
  }
}




