import React, { useState } from "react";
import "./Contact.css";
import Footer from "../../Footer/Footer";
import Navbar from "../../Navbar/Navbar";
import { Link } from "react-router-dom";
import validator from 'validator';



function Contact() {
    const handleSubmit = event => {
        event.preventDefault();
        alert('You have submitted the form.')
    };

    const [emailError, setEmailError] = useState('')
    const validateEmail = (e) => {
        var email = e.target.value

        if (validator.isEmail(email)) {
            setEmailError('Valid Email :)')
        } else {
            setEmailError('Enter valid Email!')
        }
    };

    return (
        <>
            <Navbar name3="active" />

            <section className="contactMainSection">
                <div className="container">
                    <form onSubmit={handleSubmit}>
                        <div className="row">
                            <div className="col-12 contactHeadingContainer">
                                <h1>Contact Me.</h1>
                            </div>
                            <div className="col-12 contactMailContainer">
                                <Link to="">contact@allstarspace.com</Link>
                            </div>
                        </div>
                        <div className="row input-container">
                            <div className="col-md-6 col-sm-12">
                                <div className="styled-input">
                                    <input type="text" required />
                                    <label>Name</label>
                                </div>
                            </div>
                            <div className="col-md-6 col-sm-12">
                                <div className="styled-input">
                                    <input type="text" required onChange={(e) => validateEmail(e)} />
                                    <span style={{
                                        fontWeight: 'regular',
                                        color: 'red',
                                    }}>{emailError}</span>
                                    <label>Email</label>
                                </div>
                            </div>
                            <div className="col-xs-12">
                                <div className="styled-input wide">
                                    <textarea required></textarea>
                                    <label>Message</label>
                                </div>
                            </div>
                            <div className="col-xs-12">
                                <button className="send">Send</button>
                            </div>
                        </div>
                    </form>
                </div>
            </section>

            <Footer />
        </>
    );
}

export default Contact;
