import React from 'react';
import Navbar from '../Navbar/Navbar';
import FixedYt from '../Fixed-yt/FixedYt';
import ShopPage from '../ShopPageComponents/ShopPage/ShopPage';
import Footer from '../Footer/Footer'

function Shop() {
    return (
        <>
            <Navbar name2="active"/>
            <FixedYt />
            <ShopPage />
            <Footer />
        </>
    )
}

export default Shop
