import React from 'react';
import FixedYt from '../Fixed-yt/FixedYt';
import BlogNavigation from '../BlogComponents/BlogCategoryNavigation/BlogNavigation';
import BlogTopSection from '../BlogComponents/Blog_Top_Section/BlogTopSection';
import BlogSection from '../BlogComponents/BlogSection/BlogSection';
import Footer from '../Footer/Footer';
import Navbar from '../Navbar/Navbar';
import pack from "../../../package.json"
import { useState, useEffect } from "react";
import axios from 'axios'
function Blog() {
    const [postDetail,setPostDetail]=useState([])

    useEffect(()=>{
        const fetchPostData=async()=>{
            const res1=await axios.get(`${pack.proxy}post`)
            setPostDetail(res1.data)
            console.log(res1.data);
        }
        fetchPostData();
    },[])
    
    return (
        <>
            <Navbar name1="active"/>
            <FixedYt />
            <BlogTopSection />
            <BlogNavigation />
            <BlogSection postDetail={postDetail} />
            <Footer />
        </>
    )
}

export default Blog
