import React from "react";
import "./BlogTopSection.css";
import BlogJensImg from "../../assets/img/Blog_Jens_Img.png";

function BlogTopSection() {
    return (
        <>
            <section className="blogTopSection m-0 p-0">
                <div className="container-fluid">
                    <div className="row">
                    <div className="col-12 col-lg-4 jensBlogTopImg">
                            <img src={BlogJensImg} alt="Jens Image" className="img-fluid"/>
                        </div>
                        <div className="col-12 col-lg-6 offset-lg-1 topBlogContent">
                            <h1 className="overlayHeading">
                                Welcome to My <span>News</span> Page
                            </h1>
                            <p>Curious to see new and engaging video content? Want to catch up on the latest viral clips? Take a look below to find all that you have been searching for, and more. There’s something for everyone to enjoy, so start browsing now.</p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default BlogTopSection;
